.footer {
    background-color: #f8f9fa; /* Light background */
    padding: 20px;
    text-align: center;
    width: 100%;
    position: relative;
  }
  
  .social-links {
    margin-bottom: 10px;
  }
  
  .social-links a {
    margin: 0 10px;
  }
  
  .social-links img {
    width: 50px; /* Adjust icon size */
    height: 50px; /* Adjust icon size */
  }
  
  /* Ensure the footer sticks to the bottom of the page */
  html, body {
    height: 100%;
    margin: 0;
  }
  
  .app {
    display: flex;
    flex-direction: column;
    min-height: 100vh; /* Full height of viewport */
  }
  
  .content {
    flex: 1; /* Allow content to grow and fill available space */
  }
  