/* Form container styling */
form {
  max-width: 600px;
  margin: 50px auto;
  padding: 30px;
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* Form heading styling */
form h2 {
  text-align: center;
  margin-bottom: 20px;
  color: #333;
}

/* Input fields styling */
input[type="text"],
input[type="number"],
input[type="email"],
textarea {
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
  box-sizing: border-box;
}

/* Input fields focus state */
input[type="text"]:focus,
input[type="number"]:focus,
input[type="email"]:focus,
textarea:focus {
  border-color: #007bff;
  outline: none;
}

/* Textarea styling */
textarea {
  resize: vertical;
  height: 100px;
}

/* Button styling */
button {
  display: block;
  width: 100%;
  padding: 12px;
  background-color: #d81b60;
  color: white;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s;
}

/* Button hover state */
button:hover {
  background-color: #ad1457;
}

/* Media query for smaller screens */
@media (max-width: 768px) {
  form {
    padding: 20px;
  }

  input[type="text"],
  input[type="number"],
  input[type="email"],
  textarea {
    font-size: 14px;
  }

  button {
    font-size: 14px;
  }
}
